h1.title {
    font-family: "monospace";
    color: #011f4b;
}

h2 {
    color: #03396c;
}

i {
    color: black;
}

.editor {
    margin: 10px 10px;
    border: none;
} 

textarea{
    font-family: "monospace";
    border-radius: 5px;
}

textarea:focus {
    border-color: #011f4b;
}

div.row div.btn-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-family: "monospace";
    color: #03396c !important;
}

.btn {
    margin: 10px auto !important;
    -webkit-transition: background-color .4s;
    -o-transition: background-color .4s;
    transition: background-color .4s;
}

.btn:hover {
    -webkit-transition: background-color .4s;
    -o-transition: background-color .4s;
    transition: background-color .4s;
}


.btn:focus, .btn:active {
  outline: none !important;
}



